<template>
  <div id="app">
    <Header/>
    <router-view></router-view>
  </div>
</template>

<script>

import Header from './components/blogheader'
import blogWriten from './components/blogWriten'
import blogView from './components/blogView'
import SingleBlog from './components/SingleBlog'


export default {
  name: 'app',
  components: {
    Header,
    blogWriten,
    blogView,
    SingleBlog
  }
}
</script>

<style>
  ul{
    list-style: none;
    text-align: center;
    margin: 0;
  }
  li{
    display: inline-block;
    margin: 0 10px;
  }
  a{
    color: #fff;
    text-decoration: none;
    padding: 12px;
    border-radius: 5px;
  }
  nav{
    background: crimson;
    padding: 30px 0;
    margin-bottom: 40px;
  }
  .router-link-active{
    background: rgba(255,255,255,0.8);
    color: #444;
  }
</style>



