<template>
    <div id="single-blog">
      <h1>{{blog.title}}</h1>
      <article>{{blog.body}}</article>
    </div>
</template>

<script>
    import  axios from 'axios';
    export default {
        name: "single-blog",
        data(){
          return{
            id:this.$route.params.id,
            blog:{}
          }
        },
        created() {
         // this.$http.get('https://jsonplaceholder.typicode.com/posts/'+this.id)
          axios.get('https://jsonplaceholder.typicode.com/posts/'+(parseInt(this.id)+1))
            .then((data)=> {
              console.log(data)
              this.blog = data.data;
            })
        }
    }
</script>

<style scoped>
  #single-blog{
    max-width: 960px;
    margin: 0 auto;
    padding: 20px;
    background: #eee;
    border: 1px dotted #aaa;
  }
</style>



